<template>
  <div id="createUser" style="display: inline-block;">
    <v-icon :title="$t('createUser')" v-text="icon" @click.stop="dialog = true" />
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
            <v-card-title>
              <span class="headline">{{$t('createUser')}}</span>
            </v-card-title>

            <v-card-text>
              <v-form v-model="valid" :lazy-validation="lazy">
                <v-container>
                    <v-row>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.username" required :rules="nameRules" label="*用户名"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.password" required :rules="nameRules" label="*密码" type="Password"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.fullname" required :rules="nameRules" label="*姓名"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.phone" label="联系方式" :rules="phoneRules"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.email" label="邮箱" :rules="emailRules"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-text-field v-model="createItem.address" label="地址"></v-text-field>
                        </v-col>
                        <v-col cols="6" sm="6" md="6">
                            <v-select
                              v-model="createItem.sex"
                              :items="['男', '女']"
                              label="性别"
                            ></v-select>
                        </v-col>   
                        <v-col cols="6" sm="6" md="6">
                            <Select-Roles
                              ref="selectRoles"
                            />
                        </v-col>
                        <v-col cols="12" sm="12" md="12">
                            <Select-DeptAnd-Positions 
                                :departments="departments"
                                :selectDeptAndPositionNodes="selectDeptAndPositionNodes"
                            />
                        </v-col>
                    </v-row>
                </v-container>
              </v-form>
            </v-card-text>

            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click.stop="close">取消</v-btn>
            <v-btn color="blue darken-1" text :disabled="!valid" @click.stop="save">保存</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {required, email, phone} from '@/utils/validate'

export default {
  data: () => ({
    icon: "mdi-account-plus",
    dialog: false,
    valid: true,
    lazy: false,
    createItem: {
      id: "",
      username: "",
      password: "",
      fullname: "",
      phone: "",
      sex: "",
      email: "",
      address: "",
    },
    
    selectDeptAndPositionNodes: [],

    //表单验证
    nameRules: [required],
    phoneRules: [phone],
    emailRules: [email],
  }),

  props: {
    departments: {
      type: Array
    },
    deptArray: {
      type: Array
    }
  },
  
  mounted(){
    this.initSelectDeptAndPositionNodes();
  },

  computed: {
    
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    deptArray(val, newVal) {
      this.initSelectDeptAndPositionNodes();
    }
  },

  created() {

  },

  methods: {
    initSelectDeptAndPositionNodes() {
      this.selectDeptAndPositionNodes = [];
      let _this = this;
      this.deptArray.forEach(function(selection, index){
        let tempSelection = Object.assign({}, selection);
        tempSelection.positions = [];
        _this.selectDeptAndPositionNodes.push(tempSelection);
      });
    },

    clearParams() {
        this.initSelectDeptAndPositionNodes();
        this.createItem = {};
        this.$refs.selectRoles.roleIds = [];
    },

    close() {
      this.dialog = false;
      this.clearParams();
    },

    save() {
        // 新建用户
        let params = {
          user: this.createItem,

          roleIds: this.$refs.selectRoles.roleIds,
          selectDeptAndPositionNodes: this.selectDeptAndPositionNodes
        };

        this.$http
          .post("/user/api/v1/user/insert", params)
          .then(res => {
            if (res.success) {
              this.$emit("loadUsers");
              res.msg = "用户新建成功";
            }
            this.$store.commit("SET_SNACK", res);
          })
          .catch(error => {
            this.$store.commit("SET_SNACK", {
              success: false,
              msg: "访问异常：用户未新建"
            });
          });

        this.close();
    }
  },

  components: {
    SelectDeptAndPositions: () => import("./SelectDeptAndPositions"),
    SelectRoles: () => import("@/views/dashboard/component/roles/selectRoles"),
  }
};
</script>

